import * as React from "react"
import "../styles/global.css"
import "../styles/index.css"
import Navbar from "../components/Navbar"
import AnimateInViewSection from "../components/AnimateInViewSection"

//TODO: Animate background? Floating Bubbles of random size?
//TODO each section and id can be done with mdx
const IndexPage = () => {
  return (
    <main>
      <Navbar/>
      <div className="flex flex-col overflow-hidden justify-center content-center min-h-screen bg-darkNavy cursor-default">
        <section id="intro" className="flex flex-col min-h-screen justify-center content-center text-center">
          <div className="text-5xl font-extrabold slide-in-from-left">
            <span className="bg-clip-text text-transparent bg-gradient-to-l from-green-400 to-blue-500">
              Hey, I'm Nathan
            </span>
          </div> 
          <div className="mt-5 text-xl text-turquoise slide-in-from-right">
            <span>
              How does one simply write about themselves?
            </span>
          </div>
        </section>
        <section id="about" className="flex flex-col min-h-screen justify-center content-center mx-5 md:mx-56">
          <AnimateInViewSection animName="slide-in-from-left">
            <div className="text-5xl font-extrabold">
              <span className="bg-clip-text text-transparent bg-gradient-to-l from-green-400 to-blue-500">
                About
              </span>
            </div> 
            <hr className="left-align"/>
          </AnimateInViewSection>
          <AnimateInViewSection animName="slide-in-from-right">
            <div className="mt-5 text-xl text-turquoise intro-quote">
              <span>
              Lorem ipsum dolor sit amet, consectetur adipiscing elit. Aenean interdum faucibus imperdiet. Sed convallis ultrices neque, a blandit eros iaculis ac. Pellentesque eu lacus non urna fringilla iaculis et ac nulla. Nulla tempus feugiat scelerisque. Aenean ac mattis sapien. Maecenas eget urna eget urna elementum tincidunt. Suspendisse vulputate commodo eros vel pretium. Cras eleifend luctus diam, sed consequat mauris viverra a.
              </span>
            </div>
          </AnimateInViewSection>
        </section>
      </div>
    </main>
  )
}

export default IndexPage
